@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
.gpt3__blog{
    display : flex;
    flex-direction : column;
    grid-gap : 4rem;
    gap : 4rem;
    align-items : flex-start;
    justify-content : space-between;
}

.gpt3__blog-title {
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    font-family: var(--font-family);
    max-width : 660px;  
}

.gpt3__blog-article__container {
    display : flex;
    grid-gap : 3rem;
    gap : 3rem;
    justify-content : space-between;
}  

.gpt3__blog-article__container-2{
    display : grid;
    grid-template-columns: 1fr 1fr;
    grid-gap : 2rem;
    gap : 2rem;
}

.gpt3__blog-article__container-1 {
    flex : 4 1;
}

.gpt3__blog-article__container-2 {
    flex : 6 1;
    display : grid;

}

@media screen and (max-width : 1050px) {
    .gpt3__blog-article__container {
        flex-direction: column;
    }
}

@media screen and (max-width : 630px) {
    .gpt3__blog {
        margin : 5rem 2rem !important;
    }
    .gpt3__blog-article__container-2 {
        grid-template-columns: 1fr;
    }
}
.gpt3__article {
    display: flex;
    flex-direction: column;
    justify-items: flex-start;
    align-items: flex-start;
    width: 100%;
    height: 100%;
}

.gpt3__article img {
    width: 100%;
}

.gpt3__article-content {
    padding: 1rem;
    height: 100%;
    width : 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    background : var(--color-blog);
    grid-gap : 3rem;
    gap : 3rem;

}

.gpt3__article-content__head {
    display: flex;
    flex-direction: column;
    grid-gap: 0.5rem;
    gap: 0.5rem;
}

.gpt3__article-content__head-date {
    font-weight: 700;
    font-size: 11.649px;
    line-height: 35px;
    font-family: var(--font-family);
    color: white;
}

.gpt3__article-content__head-title {
    font-weight: 800;
    font-size: 25.11px;
    line-height: 30px;
    font-family : var(--font-family);
    color : white;
}

.gpt3__article-content__read {
    color : white;
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 11.649px;
    line-height: 35px;
}
.gpt3__brand {
    display : flex;
    justify-content: center;
    align-items : center;
    flex-wrap : wrap;
}

.gpt3__brand-image {
   max-width : 150px;
   flex : 1 1;
   display : flex;
   align-items : center;
   margin : 1rem;
   justify-content: center;
}
.gpt3__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 6rem;
}

.gpt3__navbar-links {
    flex: 1 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.gpt3__navbar-links_logo {
    margin-right: 2rem;
}

.gpt3__navbar-links_logo img {
    width: 62.56px;
    height: 16.02px;
}

.gpt3__navbar-links_contanier {
    display: flex;
    flex-direction: row;
}

.gpt3__navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.gpt3__navbar-links_contanier p,
.gpt3__navbar-sign p,
.gpt3__navbar-menu_container p {
    color: White;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    margin: 0 1rem;
    cursor: pointer;
    text-transform: capitalize;
}

.gpt3__navbar-sign button,
.gpt3__navbar-menu_container button {
    font-size: 18px;
    color: white;
    /* padding : 1rem 2rem;   */
    padding: 0.5rem 1rem;
    background: #FF4820;
    border-radius: 5px;
    border: 0;
    font-family: var(--font-family);
    font-weight: 500;
    line-height: 25px;
    cursor: pointer;
}

.gpt3__navbar-menu {
    margin-left: 1rem;
    display: none;
    position: relative;
}

.gpt3__navbar-menu svg {
    cursor: pointer;
}

.gpt3__navbar-menu_container {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-end;
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: -40px;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
}

.gpt3__navbar-menu_container p {
    margin: 1rem 0;
}

.gpt3__navbar-menu_container-links-sign {
    display: none;
}

@media screen and (max-width : 1050px) {
    .gpt3__navbar-links_contanier {
        display: none;
    }

    .gpt3__navbar-menu {
        display: flex;
    }
}

@media screen and (max-width : 700px) {
    .gpt3__navbar {
        padding: 2rem 4rem;
    }
}

@media screen and (max-width : 700px) {
    .gpt3__navbar {
        padding: 2rem 2rem;
    }

    .gpt3__navbar-sign {
        display : none;
    }

    .gpt3__navbar-menu_container {
        top : 20px;
    }

    .gpt3__navbar-menu_container-links-sign{
        display :block;
    }
}
.gpt3__cta {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 3rem 2rem;
    background: var(--gradient-bar);
    border-radius: 10px;
}

.gpt3__cta-info {
    display: flex;
    flex-direction: column;
}

.gpt3__cta-info__early {
    font-weight: 500;
    font-size: 14px;
    line-height: 30px;
    color: black;
    font-family: var(--font-family);
}

.gpt3__cta-info__register {
    font-weight: 800;
    font-size: 24px;
    line-height: 45px;
    color : black;
    font-family : var(--font-family);
}

.gpt3__cta-getstart button{
    background : black;
    height : 60px;
    width : 190px;
    border-radius: 50px;
    color : white;
    font-weight : 400;
    font-size : 16px;
    border : none;
    cursor : pointer;
}

@media screen and (max-width : 750px){
    .gpt3__cta {
        flex-direction : column;
        grid-gap : 2rem;
        gap : 2rem;
        padding : 1rem 2rem;    
        text-align: center;
    }
}

@media screen and (max-width : 630px){
    .gpt3__cta {
        margin: 3rem 2rem !important;
    }
}
.gpt3__feature {
    display : flex;
    flex-direction : column;
}

.gpt3__feature-container__feature {
    display : flex;
    justify-content : space-between;
    align-items : flex-start;
    grid-gap : 10%;
    gap : 10%;
}

.gpt3__feature-container__feature-title {
    color: white;
    font-weight: 800;
    font-size: 10px;
    line-height: 24px;
    font-family: var(--font-family);
    display: flex;
    justify-content: flex-start;
    min-width : 240px;
}

.gpt3__feature-container__feature-text {
    color: var(--color-text);
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    font-family: var(--font-family);
}

.gpt3__feature-box {
    width : 38px;
    height : 3px;
    background : var(--gradient-text);
    margin-bottom : 0.5rem;
}

@media screen and (max-width : 720px)
{
    .gpt3__feature-container__feature {
        flex-direction : column;
    }
    .gpt3__feature-container__feature-title {
        margin-bottom : 0.5rem;
    }
}
.gpt3__features {
    display : flex;
    justify-content: space-between;
    align-items : flex-start;
    grid-gap : 10%;
    gap : 10%;
}

.gpt3__features-topic {
    width : 35%;
}

.gpt3__features-feature {
    flex : 1 1;
    display : flex;
    flex-direction : column;
    justify-content: space-between;
    align-items : center;
    grid-gap : 2rem;
    gap : 2rem;
}

.gpt3__features-topic h1 {
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
    font-family: var(--font-family);
    margin-bottom : 1.5rem;
}

.gpt3__features-topic p{
    color : var(--color-subtext);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    font-family : var(--font-family);
}

@media screen and (max-width : 1050px){
    .gpt3__features {
        padding : 2rem 3rem !important;
        flex-direction : column;
        grid-gap : 5rem;
        gap : 5rem;
    }
    .gpt3__features-topic {
        width : 100%;
    }
}

@media screen and (max-width : 720px) 
{
    .gpt3__features-feature {
        grid-gap : 3rem;
        gap : 3rem;
    }
}
.gpt3__footer {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    background: var(--color-footer);
    padding: 6rem 10rem 2rem;
}

.gpt3__footer-title {
    max-width: 900px;
    margin-bottom: 4rem;
}

.gpt3__footer-title p {
    font-family: var(--font-family);
    font-size: 62px;
    line-height: 75px;
    font-weight: 800;
    text-align: center;
}

.gpt3__footer-early {
    margin-bottom: 10rem;
}

.gpt3__footer-early button {
    cursor: pointer;
    background: var(--color-footer);
    border: solid white 1px;
    padding: 1rem;
    color: white;
    font-size: 18px;
    line-height: 21px;
    font-family: var(--font-family);
}

.gpt3__footer-about__topic {
    display: flex;
    flex-direction: column;
    max-width: 168px;
    grid-gap: 2rem;
    gap: 2rem;
    margin-right: 20%;
}

.gpt3__footer-about__topic-name {
    width: 118px;
    height: 30px;
}

.gpt3__footer-about__topic-desc {
    font-size: 12px;
    line-height: 16px;
    color: white;
    font-family: var(--font-family);
}

.gpt3__footer-about {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    width: 100%;
    margin-bottom: 4rem;
}

.gpt3__footer-about__container {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.gpt3__footer-about__copyright p {
    color: white;
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
}

@media screen and (max-width : 1050px) {
    .gpt3__footer-about {
        flex-direction : column;
        align-items : center;
    }
    .gpt3__footer-early {
        margin-bottom : 6rem;
    }
    .gpt3__footer-about__topic {
        margin-right : 0;
        margin-bottom : 8rem;
    }
}

@media screen and (max-width : 930px) {
    .gpt3__footer {
        padding-left : 3rem;
        padding-right : 3rem;
    }
}

@media screen and (max-width : 550px) {
    .gpt3__footer {
        padding-left : 2rem;
        padding-right : 2rem;
    }
    .gpt3__footer-early {
        margin-bottom : 5rem;
    }
    .gpt3__footer-about__topic {
        margin-bottom : 5rem;
    }
}
.gpt3__links {
    display: flex;
    flex-direction: column;
    grid-gap : 2rem;
    gap : 2rem;
    justify-content : flex-start;
    max-width : 116px;
}

.gpt3__links-title {
    display: flex;
    flex-direction: flex-start;
    font-size: 14px;
    line-height: 16px;
    color: white;
    font-family: var(--font-family);
    font-weight: 800;
}

.gpt3__links-list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    grid-gap: 2rem;
    gap: 2rem;
}

.gpt3__links-list a {
    color: white;
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 18px;
    cursor : pointer;
}

@media screen and (max-width : 500px){
    .gpt3__links { 
        max-width : 90px;
    }
}
.gpt3__header {
    display : flex;
    flex-direction : row;
}

.gpt3__header-content{
    flex:2 1;
    display : flex;
    flex-direction: column;
    justify-content : center;
    align-items : flex-start;   
    margin-right : 5rem;
}

.gpt3__header-content h1 {
    font-family : var(--font-family);
    font-weight: 800;
    font-size : 62px;
    line-height : 75px;
    letter-spacing: -0.04em;
}

.gpt3__header-content p{
    width : 85%;
    font-family : var(--font-family);
    font-weight: normal;
    font-size : 20px;
    line-height : 27px;
    color : var(--color-text);
    margin-top : 1.5rem;
}

.gpt3__header-content__input {
    width : 90%;
    margin-top : 1.5rem;
    display : flex;
    min-height : 65px;
}

.gpt3__header-content__input input{
    flex: 3 1;
    background : #052D56;
    border : solid #052d56;
    color : var(--color-text);
    font-size : 20px;
    line-height: 27px;
    padding-left : 2rem;
    border-radius : 5px 0 0 5px;

}

::-webkit-input-placeholder {
    color : var(--color-text);
    font-size : 20px;
}

::placeholder {
    color : var(--color-text);
    font-size : 20px;
}

.gpt3__header-content__input input:focus {
    outline : none;
}

.gpt3__header-content__input button {
    flex : 1 1;
     background: #FF4820;
     color : white;
     border-radius : 0 5px 5px 0;
     border : none;
     cursor : pointer;
     font-size : 20px;
     line-height :27px;
     font-weight: 700;
     font-family: var(--font-family);
     /* line-height: 25px; */
}

.gpt3__header-content__people {
    width : 100%;
    display : flex;
    margin-top : 1.5rem;
    align-items : center;
}

.gpt3__header-content__people p{
    font-family : var(--font-family);
    color : white;
    font-size : 12px;
    margin-top : 0;
    line-height : 16px;
}

.gpt3__header-content__people img {
    margin-right : 1rem;
    width : 182px;
}

.gpt3__header-image {
    flex : 1 1;
    display : flex;
    align-items : center;
    justify-content: center;
}

.gpt3__header-image img{
    width : 150%;
    position : relative;
    top : -5%;
    left : -8%;
}

@media screen and (max-width : 1050px) {
    .gpt3__header {
        flex-direction : column-reverse;
    }

    .gpt3__header-image img{
        width : 50%;
        position :initial;
    }

    .gpt3__header-content {
        width : 100%;
        text-align: center;
        align-items: center;
    }
    .gpt3__header-content__people p{
        text-align : left;
    }

}

@media screen and (max-width : 650px) {
    .gpt3__header {
        margin : 2rem 1.5rem !important;
        padding : 0 !important;
    }
    .gpt3__header-content__people {
        flex-direction: column;
    }
    .gpt3__header-content h1 {
        font-weight: 800;
        font-size : 40px;
        line-height : 46px;
    }
    .gpt3__header-content__people p{
        font-size : 12px;
        text-align: center;
    }
    .gpt3__header-image img{
        width : 100%;
        position :initial;
    }

    .gpt3__header-content__input {
        flex-direction: column;
        align-items: center;
    }   
    .gpt3__header-content__input input {
        font-size : 12px;
        min-height : 50px;
        width : 100%;
        text-align : center;
        margin-bottom : 1.5rem;
        border-radius : 5px;
        padding-left : 0;
    } 
    ::-webkit-input-placeholder {
        font-size : 12px;
    } 
    ::placeholder {
        font-size : 12px;
    }
    .gpt3__header-content__input button {
        width : 50%;
        min-height : 40px;
        border-radius: 5px;
        font-size : 12px;
        
    }
}
.gpt3__possibility {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.gpt3__possibility-vrphoto {
    flex: 1 1;
    display: flex;
    align-items: flex-start;
    margin-right : 2rem;
}

.gpt3__possibility-info {
    flex: 1 1;
    display: flex;
    grid-gap : 1.5rem;
    gap : 1.5rem;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    /* margin-right : 10%; */
}

.gpt3__possibility-info__early1 {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: #71E5FF;
}

.gpt3__possibility-info__possibilities {
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
    font-family: var(--font-family);
}

.gpt3__possibility-info__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 30px;
    font-family : var(--font-family);
    color : var(--color-text);
}
.gpt3__possibility-info__early2 {
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color : var(--color-subtext);
    font-family : var(--font-family);
}

.gpt3__possibility-vrphoto img {
    width: 100%;
}

@media screen and (max-width : 1020px){
    .gpt3__possibility {
        flex-direction : column;
        grid-gap : 2rem;
        gap : 2rem;
    }

    .gpt3__possibility-vrphoto {
        width : 65%;
        margin : 0;
        border-radius : 5px;
    }
}

@media screen and (max-width : 550px){
    .gpt3__possibility {
        padding : 2rem 2rem !important;
    }
    .gpt3__possibility-info {
        align-items: center;
        text-align: center;
    }
}
.gpt3__wgpt3 {
    display: flex;
    padding: 3rem;
    flex-direction: column;
    border-radius: 10px;
}

.gpt3__wgpt3-content {
    display: flex;
    justify-content: space-between;
}

.gpt3__wgpt3-content__what {
    min-width: 12rem;
    margin-right: 10%;
}

.gpt3__wgpt3-content__answer {
    width: 63%;
    margin-right: 10%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.gpt3__wgpt3-content__gradline {
    width : 38px;
    height : 3px;
    background: var(--gradient-text);
}
.gpt3__wgpt3-content__what p {
    color: white;
    font-weight: 800;
    font-size: 24px;
    line-height: 75px;
    font-family: var(--font-family);
    display: flex;
    align-items: flex-start;
}

.gpt3__wgpt3-content__answer p {
    color: var(--color-text);
    font-size: 1rem;
    line-height: 30px;
    font-family: var(--font-family);
}

.gpt3__wgpt3-possibility {
    display: flex;
    margin-top: 91px;
    justify-content: space-between;
}

.gpt3__wgpt3-possibility__para {
    width: 67%;
    display: flex;
    justify-content: flex-start;
}

.gpt3__wgpt3-content__explore {
    width: 10%;
}

.gpt3__wgpt3-possibility__para p {
    font-size: 34px;
    font-weight: 800;
    font-family: var(--font-family);
}

.gpt3__wgpt3-possibility__explore p {
    color: var(--color-subtext);
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    text-align: right;
}

.gpt3__wgpt3-features {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-top: 91px;
}

.gpt3__wgpt3-features__chatbots,
.gpt3__wgpt3-features__knowledgebase,
.gpt3__wgpt3-features__education {
    width : 30%;
    display: flex;
    flex-direction: column;
    margin : 0 1rem;
}

.gpt3__wgpt3-features__chatbots-topic,
.gpt3__wgpt3-features__knowledgebase-topic,
.gpt3__wgpt3-features__education-topic {
    font-weight: 800;
    font-size: 24px;
    line-height: 75px;
    font-family: var(--font-family);
    color: white;
}

.gpt3__wgpt3-features__chatbots-desc,
.gpt3__wgpt3-features__knowledgebase-desc,
.gpt3__wgpt3-features__education-desc {
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color : var(--color-text);
    font-family : var(--font-family);
    text-align: justify;
    margin-top : 2rem;
}

@media screen and (max-width : 1050px) {
    .gpt3__wgpt3 {
        margin: 4rem !important;
        padding: 3rem !important;
    }
    .gpt3__wgpt3-content {
        flex-direction: column;
        justify-content: center;
    }

    .gpt3__wgpt3-content__what {
        width: 100%;
        margin-right: 0;
    }

    .gpt3__wgpt3-content__answer {
        width: 100%;
        margin-right: 0;
    }
    
    .gpt3__wgpt3-features {
        flex-direction : column;
    }
    .gpt3__wgpt3-features__chatbots, 
    .gpt3__wgpt3-features__education, 
    .gpt3__wgpt3-features__knowledgebase{
        width : 100%;
        margin : 0;
    }

    .gpt3__wgpt3-features__chatbots-topic,
    .gpt3__wgpt3-features__knowledgebase-topic,
    .gpt3__wgpt3-features__education-topic {
        margin-bottom : 0;
        line-height : 55px;
    }

    .gpt3__wgpt3-features__chatbots-desc,
    .gpt3__wgpt3-features__knowledgebase-desc,
    .gpt3__wgpt3-features__education-desc {
        margin-bottom : 3rem;
        margin-top : 0;
    }
}

@media screen and (max-width : 720px) {
    .gpt3__wgpt3 {
        margin: 2rem !important;
        padding: 1rem !important;
    }

    .gpt3__wgpt3-possibility {
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;margin-top : 3rem;
    }

    .gpt3__wgpt3-possibility__para {
        width: 100%;
        font-size: 30px;
    }

    .gpt3__wgpt3-possibility__explore {
        width: 100%;
        display: flex;
        margin-top: 0.5rem;
    }

    .gpt3__wgpt3-features {
        margin-top : 3rem;
    }
}
*,
*::before,
*::after {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
}

body {
    background: var(--color-bg);
}

a {
    color: unset;
    text-decoration: none;
}

.gradient__bg {

    /* ff 3.6+ */

    /* safari 5.1+,chrome 10+ */

    /* opera 11.10+ */

    /* ie 10+ */

    /* global 92%+ browsers support */
    background: radial-gradient(circle at 3% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);

}

.gradient__wgpt3
{

    /* ff 3.6+ */ 

    /* safari 5.1+,chrome 10+ */

    /* opera 11.10+ */
    background:-o-linear-gradient(136deg, rgba(14, 65, 120, 1) 0%, rgba(4, 44, 84, 1) 23%, rgba(4, 44, 84, 1) 84%, rgba(27, 119, 220, 1) 100%, rgba(27, 119, 220, 1) 100%);

    /* ie 6-9 */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#1B77DC', endColorstr='#0E4178', GradientType=1 );

    /* ie 10+ */

    /* global 94%+ browsers support */
    background:linear-gradient(136deg, rgba(14, 65, 120, 1) 0%, rgba(4, 44, 84, 1) 23%, rgba(4, 44, 84, 1) 84%, rgba(27, 119, 220, 1) 100%, rgba(27, 119, 220, 1) 100%);

}
.gradient__text {
    background: var(--gradient-text);
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
    -webkit-text-fill-color: transparent;
}

.section__padding {
    padding: 4rem 6rem;
}

.section__margin {
    margin: 4rem 6rem;
}

.scale-up-center {
    -webkit-animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
    animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

@-webkit-keyframes scale-up-center {
    0% {
        -webkit-transform: scale(0.5);
        transform: scale(0.5);
    }

    100% {
        -webkit-transform: scale(1);
        transform: scale(1);
    }
}

@keyframes scale-up-center {
    0% {
        -webkit-transform: scale(0.5);
        transform: scale(0.5);
    }

    100% {
        -webkit-transform: scale(1);
        transform: scale(1);
    }
}

/* basically for tablets   */

@media screen and (max-width : 700px) {
    .section__padding {
        padding: 4rem;
    }

    .section_margin {
        margin: 4rem;
    }

}

@media screen and (max-width : 550px) {
    .section_padding {
        padding: 4rem 2rem;
    }

    .section_margin {
        margin: 4rem 2rem;
    }
}
